"use client"
import { Avatar, Descriptions } from "antd"
import DescriptionsItem from "antd/es/descriptions/Item"
import { useUserStore } from "../../../shared/providers/userProvider"
import { AppTag } from "../../../shared/ui/AppTag/ui"
import { RoleComparer } from "../../../shared/ui/AppTag/types/RoleComparer"

export const UserProfile = () => {
  const { user } = useUserStore(state => state)
  const { name, course, role, group } = user!
  return (
    <div>
      <Avatar
        size={"large"}
        src={""}
      />
      <AppTag>{RoleComparer[role]}</AppTag>
      <Descriptions>
        <DescriptionsItem label={"ФИО"}>{name}</DescriptionsItem>
        <DescriptionsItem label={"Курс"}>{course}</DescriptionsItem>
        <DescriptionsItem label={"Группа"}>{group}</DescriptionsItem>
      </Descriptions>
    </div>
  )
}
